import { reactive, toRefs } from 'vue';
import Child from './Child.vue';
export default {
  name: 'ComA',
  components: {
    Child
  },

  setup() {
    const state = reactive({
      count: 0
    });

    function handleClk() {
      state.count++;
    }

    return {
      handleClk,
      ...toRefs(state)
    };
  }

};